@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Concert+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
    overflow: auto;
    font-family: 'Concert One', cursive;
}

.AppHeader{
    -webkit-animation: scroll 70s linear infinite;
            animation: scroll 70s linear infinite;
    background: url("https://images.unsplash.com/photo-1465146633011-14f8e0781093?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=3450&q=80"), #111111;

}

.kontain{
    height: 100vh;
    background-image: url(/Fullstack/client/basics/public/static/media/background.85cd67db.jpg);
    color: #7C83FD;
    font-family: 'Roboto Mono', monospace;
}

.kontain h1{
    float: left;
    padding: 5rem;
}

.kontain h3{
    float: right;
}


.show{
    height: auto;
    padding-top: 100px;
    padding-bottom: 300px;
}

.show2{
    height: 100%;
    padding-top: 100px;
    padding-bottom: 100px;
}

.show2 h1{
    font-family: 'Roboto Mono', monospace;
    font-size: 50px;
    padding-bottom: 50px;

}

@-webkit-keyframes scroll {
   100%{
    background-position:0px -3000px;
  }
}

@keyframes scroll {
   100%{
    background-position:0px -3000px;
  }
}

@media (prefers-reduced-motion) {
  .AppHeader {
    -webkit-animation: scroll 200s linear infinite;
            animation: scroll 200s linear infinite;
  }
}


*{
	margin:0;
	padding:0;
	box-sizing: border-box;
}

.container{
	max-width: 1170px;
	margin:auto;
}
.row{
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
}
ul{
	list-style: none;
}
li{
	text-align: justify;
}
.footer{
	background-color: #02091a;
    padding: 70px 0;
}
.footer-col{
   width: 25%;
   padding: 0 15px;
}
.footer-col h4{
	font-size: 18px;
	color: #ffffff;
	text-transform: capitalize;
	margin-bottom: 35px;
	font-weight: 500;
	position: relative;
} 
.footer-col ul li:not(:last-child){
	margin-bottom: 10px;
}
.footer-col ul li a{
	font-size: 16px;
	text-transform: capitalize;
	color: #ffffff;
	text-decoration: none;
	font-weight: 300;
	color: #bbbbbb;
	display: block;
	transition: all 0.3s ease;
}
.footer-col ul li a:hover{
	color: #ffffff;
	padding-left: 8px;
}
.footer-col .social-links a{
	display: inline-block;
	height: 40px;
	width: 40px;
	background-color: rgba(255,255,255,0.2);
	margin:0 10px 10px 0;
	text-align: center;
	line-height: 40px;
	border-radius: 50%;
	color: #ffffff;
	transition: all 0.5s ease;
}
.footer-col .social-links a:hover{
	color: #24262b;
	background-color: #ffffff;
}

@media(max-width: 767px){
  .footer-col{
    width: 50%;
    margin-bottom: 30px;
}
}
@media(max-width: 574px){
  .footer-col{
    width: 100%;
}
}
